import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';

const WorkInProgress = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="work-in-progress">
      <Title> Work in Progress</Title>
      <span> Delivering Health at School and Educational Outcomes: Evidence from Brazil</span>
      <p>(with Lia Braga and <a href='https://www.rudirocha.org/'>Rudi Rocha</a>)</p>
        <p><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Project Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "In this research project, we assess whether and how the introduction of the Health at School Program in Brazil from 2009 to 2014 had effects on educational outcomes. Health at School was designed to induce and standardize activities of primary health care professionals at public schools and to increase the provision of services involving screening, diagnosis and specific treatments for the identified health-related problems among children. We rely on a municipality-by-year panel and explore exogenous variation in the staggered process of program municipality prioritization for causal identification. Our results will bring contributions to a growing literature on the impacts of health improvements on human capital accumulation, and illustrate whether at-scale programs that explore schools as ideal loci for health activities may be considered an effective policy alternative that improves education." : ''}</p>
        <span> A Framework to Interpret Treatment Effects on Standardized Outcomes</span>
        <p></p>
        <p><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Project Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "One of the goals of applied research in economics is to provide policy-makers with information that they can interpret and act upon. However, individuals outside quantitative academia find it difficult to interpret magnitudes when they are expressed in quantitative language. Standardized variables and indicators are widely used in applied micro-econometric research but their magnitude is difficult to interpret. It is well-known that average treatment effects on standardized variables that are approximately normal can be interpreted as jumps from the median to other percentiles of the counterfactual distribution. This project main goal would be to develop a framework to provide quantile treatment effects on standardized variables a more amenable interpretation. I am still not sure whether the results of this work should appear on a paper or on a simple visualization tool, so comments at this stage would be highly appreciated." : ''}</p>
    </div>
  )
}


export default WorkInProgress;
