import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';

import ciie_rubin_selection from '../../../pdf/ciie_rubin_selection.pdf';
import ciie_rubin_selection_annbib from '../../../pdf/ciie_rubin_selection_annbib.pdf';
import ehcf_teachers_annbib from '../../../pdf/ehcf_teachers_annbib.pdf';

const Learning = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="learning">
      <Title> Learning </Title>
    <div>
      <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Read me{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ? "These are slides and annotated bibliographies (some are work in progress) for different methodological and substantive topics in applied development economics. These files gather paper references, links, abstracts and some comments (in italic) on the papers I had the chance to read and think about. Each file is organized according to a tentative taxonomy of sub-strands of literature in empirical topics and policy-oriented analysis. Please e-mail me if you have suggestions on any aspect of this material." : ''}
     </div> 
      <span> Causal Inference and Impact Evaluation </span>
        <div>
        <p> Rubin Causal Model and Selection</p> 
        <p><a href={ciie_rubin_selection} download>Slides (.pdf)</a> {' '}  </p>
        <p><a href={ciie_rubin_selection_annbib} download>Annotated Bibliography (.pdf)</a> {' '}  </p>
        <p> "Canonical" Regression Discontinuity Designs  </p>
        <p> Roy Models, Marginal and Policy Relevant Treatment Effects {' '} </p>
      </div> 
      
      <span> Economics and Policy of Human Capital Formation </span>
      <div>
        {/* <p> Canonical Concepts </p> 
        <p>  <a href={ciie_rubin_selection} download>Annotated Bibliography - Division of Labor (.pdf)</a> {' '} </p> 
        <p>  <a href={ciie_rubin_selection} download>Annotated Bibliography - Skills and Tasks (.pdf)</a> {' '} </p>  */}
        <p> Measuring Human Capital  </p> 
        <p> <a href={ehcf_teachers_annbib} download>Annotated Bibliography - Early Childhood Development: Language (.pdf)</a> {' '}</p>
        <p> Teachers  </p> 
        <p> <a href={ehcf_teachers_annbib} download>Annotated Bibliography (.pdf)</a> {' '}</p>
    </div> 
    </div>

    
  )

}
  
export default Learning;
