import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';

import droughts1 from '../../../pdf/figs/droughts1.PNG';
import droughts2 from '../../../pdf/figs/droughts2.PNG';


import audits1 from '../../../pdf/figs/audits1.PNG';
import audits2 from '../../../pdf/figs/audits2.PNG';
import audits3 from '../../../pdf/figs/audits3.PNG';

import evt1 from '../../../pdf/figs/evt1.PNG';
import evt2 from '../../../pdf/figs/evt2.PNG';
import evt3 from '../../../pdf/figs/evt3.PNG';

import cons1 from '../../../pdf/figs/cons1.PNG';
import mita1 from '../../../pdf/figs/mita_map_1.PNG';
import mita2 from '../../../pdf/figs/mita_map_2.PNG';
import class2 from '../../../pdf/figs/class2.PNG';



const NiceFiguresTables = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="nice-figures-tables">
      <Title>Policy in Brazil: A Walkthrough</Title>

      <div>
      <span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Read me{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ? "The sequence of figures and tables below try to quickly provide information on what I consider to be the most impressive results in the applied social sciences of Brazilian public policies or social programs, more generally. At the end of the sequence, there are some other figures from papers I like from elsewhere." : ''}
     </div>     

     <p> Rainfall Variation and Droughts in the Semiarid (1990s and 2000s)</p>
      <span><p>Rocha, Rudi; Soares, Rodrigo (2015). "Water Scarcity and Birth Outcomes in the Brazilian Semiarid". Journal of Development Economics.</p></span>
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      <span><p>{abstractIsVisible ? "Roughly one-third of the rural population in developing countries lives in arid and semiarid regions, facing recurrentwater scarcity. This is likely to become an even more common situationwith climate change. This paper analyzes the impact of rainfall fluctuations during the gestational period on health at birth in the Brazilian semiarid, highlighting the role of water scarcity as a determinant of early life health. We find that negative rainfall shocks are robustly correlated with higher infantmortality, lower birth weight, and shorter gestation periods. Mortality effects are concentrated on intestinal infections andmalnutrition, and are greatlyminimizedwhen the local public health infrastructure is sufficiently developed (municipality coverage of piped water and sanitation). We also find that effects are stronger during the fetal period (2nd trimester of gestation), for children born during the dry season, and for mortality immediately after birth. Our estimates suggest that expansions in public health infrastructure would be a cost-effective way of reducing the response of infant mortality to rainfall scarcity.": ''}</p></span>
      </div>
      <p><img src={droughts1} alt="graph" /></p>
      <p><img src={droughts2} alt="graph" /></p>


     <p> "Programa de Fiscalização a Partir de Sorteios Públicos" (2000s)</p>
      <span><p>Ferraz, Claudio; Finan, Frederico (2008). "Exposing Corrupt Politicians: The Effect of Brazil’s Publicly Released Audits on Electoral Outcomes". Quarterly Journal of Economics.</p></span>
      <span><p>Avis, Eric; Ferraz, Claudio; Finan, Frederico (2018). "Do Government Audits Reduce Corruption? Estimating the Impacts of Exposing Corrupt Politicians". Journal of Political Economy.</p></span>
      <span><p>Colonelli, Emanuele; Prem, Mounu (2021). "Corruption and Firms". Review of Economic Studies.</p></span>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "Implemented by the Brazilian Office of the Comptroller General (“Controladoria Geral da União”, CGU) from 2003 onwards to discourage corruption and to stimulate civic participation in the monitoring of public spending, the Programa de Fiscalização a partir de Sorteios Públicos was based on two main components: (i) selection of municipalities through lotteries, performed by a large national public bank (“Caixa Econômica Federal”) a few times a year, to investigate corruption or mismanagement of the resources transferred by the federal government - lottery dates were always unknown and the chances of being audited during a political term varied between 10% to 25% in the different states of the country; disclosure of the main findings on the internet and on major media vehicles.": ''}</p></span>
        </div>
        <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>{abstractIsVisible ? '' : ""}</span>
        <span><p>{abstractIsVisible ? "On the 2004 elections, the reelection rate of mayors involved in corruption detected by the audits was lower. This impact was monotonic in the number of acts of corruption, that is, mayors with more than 2 acts of corruption seem to have been more punished in the polls. Mayors with 0 acts of corruption, in turn, seem to have captured electoral advantage. Overall, these results highligh the value of having a more informed electorate to enhance political selection. A summary of these results can be found in the figure below": ''}</p></span>
      </div>
          <p><img src={audits1} alt="graph" /></p>
          <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>{abstractIsVisible ? ' ' : ""}</span>
          <span><p>{abstractIsVisible ? "This paper examines the extent to which government audits of public resources can reduce corruption by enhancing political and judiciary accountability. We do so in the context of Brazil’s anticorruption program, which randomly audits municipalities for their use of federal funds. We find that being audited in the past reduces future corruption by 8 percent, while also increasing the likelihood of experiencing a subsequent legal action by 20 percent.": ''}</p></span>
        </div>        
      <p><img src={audits3} alt="graph" /></p>
      <p> "Urna Eletrônica" (Eletronic Voting Technology, 1990s and 2000s) </p>
      <span><p>Fujiwara, Thomas (2015a). "Voting Technology, Political Responsiveness, and Infant Health: Evidence From Brazil". Econometrica.</p></span>
      <span><p>Fujiwara, Thomas (2015b). "Voting Technology, Political Responsiveness, and Infant Health: Evidence From Brazil". Econometrica, Online Appendix.</p></span>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "In the 1998 and 2002 Brazilian elections, the Supreme Electoral Court introduced a new electronic voting technology, originally aimed at reducing the time and costs of vote counting. The electronic ballot box [urna eletrônica] consists of a small screen and a set of keys, closely resembling a touch-tone phone with colored buttons for confirmation, correction and for casting a residual vote – defined as those that could not be assigned for any candidate. As the voting technology provides visual aids (candidates’ pictures), guides voters through the specific votes that should be casted, and gives feedback when something is wrong, it’s introduction was also likely to make the voting process easier for less educated, and especially, illiterate citizens. With the adoption of the technology by some municipalities on the 1998 elections, the valid (non-residual) votes-turnout ratio increased by roughly 10 percentage points 15% (see the figure below), and the change was higher in places with high shares of illiterate citizens – a result consistent with the hypothesis that the ballot box would, in fact, enfranchize less educated citizens (first figure below)":''}</p></span>
      </p>
      <p><img src={evt1} alt="graph" /></p>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "In the years of 1999 to 2006, the adoption of the technology across the country was associated with the election of state governments that increased in 34% the share of spending in health care (from 9.9% to 13.3% of the budget, as presented in the second figure below, in a robustness to leave-one-out state exercise). Over the same period, health care utilization and fetal health indicators in mothers without primary schooling showed improvements – for instance, the share of low-birth weights (less than 2,500 grams) decreased 7% (third figure below, robustness to leave-one-out state exercise). These figures are in the Online Appendix.":''}</p></span>
      </p>
      <p><img src={evt2} alt="graph" /></p>
      <p><img src={evt3} alt="graph" /></p>
      
      <span><p>Bruce, Raphael and Lima, Rafael (2019). "Compulsory Voting and TV News Consumption ". Journal of Development Economics.</p></span>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <span><p>{abstractIsVisible ? "Compulsory voting leads to an increase in the consumption of TV news among young voters. This effect is only present for Brazil's most watched newscast, Jornal Nacional. This effect is stronger for those who hold moderate political views regarding the incumbent government.": ''}</p></span>
      </p>
      <p><img src={cons1} alt="graph" />
      </p>
      <span >Dell, Melissa (2010). "The Persistent Effects of Peru's Mining Mita". Econometrica.</span>
        <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        <p>{abstractIsVisible ? "The borders indicate the colonial institution of mita in a sub-region of Peru, which induced a forced labor system where indigenous communities had to send one-seventh of their adult male population to work in Potosí silver and Huancavelica mercury mines. The boundaries induce discrete change that allows for a sharp regression discontinuity design using with two running variables (latitude and longitude). Melissa's papers asks, first: what are the reduced-form effects of mita on contemporary local prosperity? OK, check the figure for stunting, it's impressive, also from a magnitude of effects perspective! What can we learn about channels of institutional persistence? Very boldly, the paper questions if inequality is a useful foundation for theories of long-run growth: historical inequality increased subsequent investments in public goods. ``The evidence in this study indicates that large landowners ---while they did not aim to promote economic prosperity for the masses--- did shield individuals from exploitation by a highly extractive state and ensure public goods. Thus, it is unclear whether the Peruvian masses would have been better off if initial land inequality had been lower, and it is doubtful that initial land inequality is the most useful foundation for a theory of long-run growth. (our emphasis, p. 1866)" : ''}</p>
      </p><img src={mita1} alt="graph" />
      <img src={mita2} alt="graph" />
      <p>
      <span >Fredriksson, Peter, Öckert, Björn, Oosterbeek, Hessel. (2013). "The Long-run Effects of Class Size". Quarterly Journal of Economics.</span>
      <p><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>      </p>
        {abstractIsVisible ? "This article evaluates the long-term effects of class size in primary school using rich data from Sweden and exploiting variation in class size created by a maximum class size rule. I really like the way this table combines information on reduced-form, instrumental variables and first-stage estimates." : ''}
      </p>
      <p>
        <img src={class2} alt="graph" /> 
      </p>
    </div>
    
  )
}

export default NiceFiguresTables;
